import React,{useState} from 'react';
import data from './utils/data';
import List from './components/List'


function App() {
    const [answrs,revealer]=useState(data);
        
    return (
        <main>
            <section className='container'>
                <h3>Apple Pool Noodle Reviews</h3>
                <List answrs={answrs}/>
                <list />
            </section>
        </main>
    )
}
export default App















