import React from "react";
import RateBar from "./Rater";

const List = ({ answrs, revealer }) => {
  return answrs.map((questbox) => {                 //question
    const { id, rating, name, job, image, text} = questbox;
    //replaces rating with a random num generator
    let rating2 = (Math.floor(Math.random() * 10)/2);
 

    return (
      <article key={id} className="list">
        <img className="pfp" src={image} alt="sorry, pal" />
        <div id="ratebar" dangerouslySetInnerHTML={RateBar(rating2)}/>
    
        <div style={{ position: "relative" }}>
          <p id="nameline">{name}</p>
          <p>{job}</p>
            <p className="answer">{text}</p>
        </div>
      </article>
    );
  });
};

export default List;
