import React, { createElement } from "react";


const RateBar = (rating) =>{
let rbar = "";
for(let i = rating; i > 0; i-=1){
//so I take the rating number & for every 1, I spawn a star. anything below 1 is assumed a half star
if(i < 1){
rbar = rbar + `<img id="star" src="./stariconhalf.png">`;
}else{
rbar = rbar + `<img id="star" src="./staricon.png"> `;
}

console.log(i)
}
return {__html: rbar}

}


export default RateBar; 